<template>
  <div class="teacher">







      <el-form :inline="true" class="demo-form-inline" :loading="loading">
        <el-form-item label="日期">
          <el-date-picker
            v-model="date"
            type="month"
            size="mini"
            placeholder="选择月">
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button  size="mini" @click="download">下载数据</el-button>
        </el-form-item>

      </el-form>


      <el-table
        :data="list"
        style="width: 100%"
        border

        size="mini"
        v-loading="loading"
      >
        <el-table-column v-for=" h in header " :prop="h" :label="h "></el-table-column>
      </el-table>

  </div>
</template>

<script>


  import request from './config'

  import req from 'axios'
  import moment from 'moment'
  import download from './excel-download.vue'
export default {
  components: {
    download,
},
  name: 'data-export',
  data () {
    return {
      school_id:window.school_id,
      list:[],
      header:[],
      total:0,
      search:'',
   page_size:10,
      page_num:1,
      loading:false,
      date: moment().subtract(1,'month').format('YYYY-MM'),

    }
  },

  watch:{

      date(){
        this.fetch()
      }
  },

  computed:{
    start(){
      return  moment(this.date).format('YYYY-MM-DD')

    },
    end(){
      return moment(this.date).add(1,'month').format('YYYY-MM-DD')
    }
  },

  methods:{

    download(){
      location.href = `/api2/export/?start=${this.start}&end=${this.end}`
    },
    handlePreview(){},
    handleRemove(){},
      change_page(p){
          console.log(p)
      },



      async fetch(){
        
             var 
              search = this.search,
              start = this.start,
              end = this.end,
              that = this

          this.loading = true
  
          var response = await request.get('/api/excel/',{
            params:{
              start:start,
              end:end,
      
            }
          })
          this.header =[]
          var exam = response.data.exam
          var list = []
          for(var i=0;i<response.data.list.length;i++){
            var item = response.data.list[i]
            item.exam_list = item.exam_list || [{id:item.exam_id,total:item.total,desc:item.desc}]
            var same_teacher = list.find(function (obj) {
              return obj.teacher_id == item.teacher_id
            })
            if(same_teacher){
              same_teacher.exam_list.push({id:item.exam_id,total:item.total,desc:item.desc})
            }else {
              list.push(item)
            }
          }

        that.header.push('序号')
        that.header.push('姓名')
        that.header.push('身份证')
        that.header.push('银行卡号')
      
        that.header.push('电话')
        that.header.push('所属学校')

        response.data.exam.map(function (obj) {
          that.header.push(obj.desc)

        })
        that.header.push('总计')
          list = list.map(function (item,index) {
            response.data.exam.map(function (obj) {
              item[obj.desc] = 0

            })
            item.exam_list.map(function (obj) {
              item[obj.desc] = obj.total
            })

            var sum = 0
            for(var i=0;i<item.exam_list.length;i++){
              sum+=item.exam_list[i].total
            }

            item['总计']=sum
            item['序号']=(page_size*(page_num-1))+ index+1 
            item['身份证']=item.teacher.idcard
            item['银行卡号']=item.teacher.bankcard
            item['姓名']=item.teacher.name
            item['电话']=item.teacher.phone
            item['所属学校']=item.teacher.school_name

            delete item.teacher
            delete item.desc
            delete item.exam_list
            delete item.create_time
            delete item.exam_id
            delete item.school_total
            delete item.total
            delete item.teacher_id
            delete item.update_time
            return item
          })

          console.log(list,'list')

          this.list = list


   
        this.loading = false
      }



  },
  mounted(){
      this.fetch()
  }


}

</script>

